<template>
	<div>
		<p>获取到的子组件的值是 {{childValue}} <child v-on:post="getchildValue"></child></p>
		<footer-nav v-bind:class="{'isIndex':isNowPage}"></footer-nav>
	</div>
</template>

<script>
	import FooterNav from '../../components/footer.vue'
	import Parmas from '../parmas.vue'
	export default{
		components:{
			FooterNav,
			'child':Parmas
		},
		data(){
			return {
				isNowPage:true,
				childValue:""
			}
		},
		methods:{
			getchildValue(v){
				this.childValue = v;
			}
		}
	}
</script>

<style>
	
</style>


