import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Index from '../views/index/index'
import Manage from '../views/manage/index'
import Parmas from '../views/parmas'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index,
      children:[{
      	path: 'parmas',
	      name: 'Parmas',
	      component: Parmas,
	      meta:{
	      	title:"子页面"
	      }
      }],
      beforeEnter (to,from,next){
      	console.log(to);
      	console.log(from);
      	if(to.meta.title){
      		document.title = to.meta.title
      	}else{
      		document.title = "dom"
      	}
      	next();
      },
      meta:{
      	title:'首页'
      }
    },
    {
    	path:"/manage",
    	name:"Manage",
    	component:Manage,
    	meta:{
    		title:"管理"
    	},
      beforeEnter (to,from,next){
      	if(to.meta.title){
      		document.title = to.meta.title
      	}else{
      		document.title = "dom"
      	}
      	next();
      }
    },
    {
		  path:'*',
		  redirect: '/'
		},
  ]
})



