<template>
	<div class="manage tc">
		<button @click="add">新增</button>
		<div class="input-area" v-show="showAdd">
			<input type="text" placeholder="请输入姓名" v-model="nameValue"/>
			<button v-on:click="addName">确定</button>
		</div>
		<table>
			<tr>
				<th>姓名</th>
				<th>操作</th>
			</tr>
			<tr v-for="(item,index) in peoples">
				<td>
					{{item.name}}
				</td>
				<td v-bind:id="index">
					<span v-on:click="edit">编辑</span>
					<span v-on:click="del">删除</span>
				</td>
			</tr>
		</table>
		<div class="wrap" v-show="showEdit">
			<div class="content">
				<input type="text" placeholder="请输入姓名" v-model="editName" />
				<button v-on:click="cancle">取消</button>
				<button v-on:click="editEnsure">确定</button>
			</div>
		</div>
		<footer-nav v-bind:class="{'isManage':isNowPage}"></footer-nav>
	</div>
</template>

<script>
	import FooterNav from '../../components/footer.vue'
	export default{
		components:{
			FooterNav
		},
		data(){
			return {
				isNowPage:true,
				peoples:[{"name":"李四"},{"name":"张三"}],
				showAdd:false,
				nameValue:"",
				showEdit:false,
				editName:"",
				editId:0
			}
		},
		methods:{
			add(){
				this.showAdd = true;
			},
			addName(){
				var v = this.nameValue;
				console.log(v.trim())
				if(v.trim() == ""){
					alert('请输入新增人员姓名')
				}else{
					var data = {};
					data.name = v;
					this.peoples.push(data);
					this.showAdd = false;
				}
			},
			del(e){
				var id = e.target.offsetParent.id;
				this.peoples.splice(id,1);
			},
			edit(e){
				var id = e.target.offsetParent.id;
				this.showEdit=true;
				this.editName = this.peoples[id].name;
				this.editId = id;
			},
			cancle(){
				this.showEdit=false;
			},
			editEnsure(){
				var v = this.editName;
				if(v.trim()==""){
					alert('请输入姓名')
				}else{
					this.peoples[this.editId].name = v;
					this.showEdit=false;
				}
			}
		}
	}
</script>

<style scoped>
	button{
		width: 80px;
		height: 35px;
		background: #42B983;
		color: #fff;
		border: none;
		border-radius: 3px;
		outline: none;
	}
	.input-area input{
		border: 1px solid #ddd;
		border-radius: 3px;
		height: 35px;
		padding-left: 10px;
		outline: none;
	}
	table{
		border-spacing: 0;
		border-collapse: collapse;
		width: 100%;
		margin-top: 15px;
		margin-bottom: 50px;
	}
	table tr th{
		background: #ddd;
	}
	table tr{
		height: 35px;
		line-height: 35px;
	}
	table tr td{
		border: none;
	}
	table tr td span{
		padding: 5px;
	}
	table tr td span:hover{
		cursor: point;
		background: #42B983;
		color: #fff;
		border-radius: 2px;
	}
	.wrap {
		position: fixed;
		top: 0;
		left: 0;
		z-index: 1;
		width: 100%;
		height: 100%;
		background-color: rgba(51,51,51,0.5);
	}
	.content{
		position: absolute;
		z-index: 2;
		width: 200px;
		left: 50%;
		top: 40%;
		margin-left: -100px;
	}
	.content input{
		width: 180px;
		margin-bottom: 10px;
		border: 1px solid #ddd;
		border-radius: 3px;
		height: 35px;
		padding-left: 10px;
		outline: none;
	}
	.content button{
		width: 90px;
	}
</style>


