<template>
	<div id="child">
		<input type="texts" placeholder="输入需要传递的值" v-model.trim="setValue" />
		<button v-on:click="change">传递</button>
	</div>
</template>

<script>
	import FooterNav from '../components/footer.vue'
	export default{
		components:{
			FooterNav
		},
		data(){
			return {
				setValue:''
			}
		},
		methods:{
			change(){
				if(this.setValue.length>=1){
					this.$emit("post",this.setValue);
				}else{
					alert('请输入传递的值')
				}
				
			}
		}
	}
</script>

<style scoped>
	input{
		border: 1px solid #ddd;
		padding-left: 10px;
		height: 35px;
		border-radius: 3px;
		outline: none;
	}
	button{
		width: 80px;
		height: 35px;
		border:none;
		outline: none;
		background: #41B883;
		color: #fff;
		border-radius: 5px;
	}
</style>


